@import url(https://fonts.googleapis.com/css2?family=Berkshire+Swash&family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap);
/* FLEX ROWS */
.fl-row-cen-cen {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.fl-row-start-cen {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.fl-row-cen-start {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
}

.fl-row-between-cen {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

/* FLEX COLUMNS */

.fl-col-cen-cen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.fl-col-start-cen {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.fl-col-cen-start {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.fl-col-between-cen {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

/* FLEX SELF-ALIGNS */
.self-start {
  align-self: flex-start;
}
.self-end {
  align-self: flex-end;
}
.w-10 {
  width: 10%;
}
.w-20 {
  width: 20%;
}
.w-25 {
  width: 25%;
}
.w-48 {
  width: 48%;
}
.w-50 {
  width: 50%;
}
.w-75 {
  width: 75%;
}
.w-100 {
  width: 100%;
}
.w-auto {
  width: auto;
}
.min-w-100 {
  min-height: 100vw;
}
.min-h-100 {
  min-height: 100vh;
}

/* PRESETS/GLOBAL STYLES */
:root {
  --chelsea-blue: #034694;
  --azure: #007fff;
  --capri: #00bfff;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html {
  font-size: 16px;
  height: 100%;
}
body {
  color: #f5f5f5;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  min-height: 100%;
}
a {
  color: #00bfff;
  color: var(--capri);
  text-decoration: none;
  font-weight: 500;
}
a:hover {
  color: #007fff;
  color: var(--azure);
  text-decoration: underline;
}
p {
  font-weight: 300;
  margin-top: 1rem;
}
li {
  list-style: none;
}
form input {
  border: 1px solid gray;
  border-radius: 4px;
  background-color: black;
  color: white;
  font-size: 1rem;
  font-weight: 500;
  margin-top: 1rem;
  padding: 1rem 0.5rem;
  outline: 0;
  transition: 0.3s ease;
  width: 100%;
}
form input:focus {
  border: 1px solid transparent;
  border-bottom: 1px solid #007fff;
  border-bottom: 1px solid var(--azure);
  border-radius: 0px;
}
form input:focus::-webkit-input-placeholder {
  color: #00bfff;
  color: var(--capri);
}
form input:focus:-ms-input-placeholder {
  color: #00bfff;
  color: var(--capri);
}
form input:focus::placeholder {
  color: #00bfff;
  color: var(--capri);
}
button {
  border: 1px solid gray;
  border-radius: 50px;
  background-color: black;
  color: white;
  cursor: pointer;
  font-size: 1rem;
  margin-top: 1rem;
  padding: 0.75rem 2rem;
  outline: 0;
  transition: 0.3s ease;
  width: 100%;
}
button:hover {
  border: 1px solid lightgray;
  background-color: rgb(25, 25, 25);
}
button.primary {
  border: 1px solid transparent;
  background-color: #034694;
  background-color: var(--chelsea-blue);
}
button.primary:hover {
  background-color: #007fff;
  background-color: var(--azure);
}
.hr {
  border-top: 1px solid #2e2e2e;
  margin-top: 1rem;
}

.homepage {
  min-height: 100vh;
  width: 100%;
  display: grid;
  grid-template-columns: 0.25fr 0.5fr 2fr 1fr 0.25fr;
  grid-template-rows: auto;
}

.sidebar {
  position: fixed;
  right: 0;
  top: 0;
  width: 25%;
  /* grid-column: 4; */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.sidebar-list {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}
.sidebar-list li {
  display: flex;
  padding: 1.25rem 2rem 1.25rem 1rem;
  align-items: center;
  max-width: auto;
}
.sidebar-list a:hover {
  background-color: #151515;
  cursor: pointer;
}
.sidebar-icon {
  font-size: 2rem;
  margin-right: 1.25rem;
}
.sidebar-list a {
  border-radius: 50px;
  font-size: 1.25rem;
  font-weight: 700;
  width: 100%;
  color: #f5f5f5;
  text-decoration: none;
  transition: 0.2s ease-in-out;
}
.sidebar-list a.primary {
  background-color: var(--chelsea-blue);
  width: 60%;
}
.sidebar .new-post {
  align-self: flex-start;
  width: 50%;
}

.posts {
  grid-column: 3;
}

.App {
  background-color: black;
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.postic {
  font-family: 'Berkshire Swash', sans-serif;
  font-size: 4rem;
}
.main-page,
.login-page,
.signup-page {
  padding: 2rem;
}
.main-page {
  min-height: 100vh;
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.main-heading {
  font-weight: 800;
  margin-bottom: 2rem;
}
.main-login-section {
  margin-top: 2rem;
}
.main-login-section button {
  margin-top: 0.5rem;
}
.signup-page {
  min-height: 100vh;
  width: 25%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.signup-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.signup-form .inline {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
@media only screen and (max-width: 600px) {
  .main-page,
  .login-page,
  .signup-page {
    width: 100%;
  }
  .login-form,
  .signup-form {
    width: 100%;
  }
}
@media only screen and (min-width: 600px) {
  .main-page,
  .login-page,
  .signup-page {
    width: 60%;
  }
  .login-form,
  .signup-form {
    width: 100%;
  }
}

@media only screen and (min-width: 992px) {
  .main-page,
  .login-page,
  .signup-page {
    width: 45%;
  }
  .login-form,
  .signup-form {
    width: 100%;
  }
}
@media only screen and (min-width: 1200px) {
  .main-page,
  .login-page,
  .signup-page {
    width: 35%;
  }
  .login-form,
  .signup-form {
    width: 100%;
  }
}

